import React, { useState } from 'react';
import {Container, Row, Col} from 'react-bootstrap';
import programmer from './Assets/illustration_green.png';
import Typing from './typing';
import arrow from './Assets/arrow.png';

const Home = () => {
    // xs={the number of columns you wish to span for Extra small devices Phones (<768px)}
    // md={the number of columns you wish to span for Medium devices Desktops (‚â•992px)}
    const [startDescribe, setDescribe] = useState(false);
    return(
        <section>
            <Container className="home">
                <Row>
                    {/*One col for name*/}
                    <Col md={7} className="homeColumn1">
                        <h1 className="greeting">Hello<span className="wavingHand"> üëã</span>, there!</h1>
                        <h1 className="intro">I am <span className="fullName">Kush Arora.</span></h1>
                        {startDescribe ? <Typing /> : null}
                        <button className="homeBtn" onClick={() => {
                            if(!startDescribe) {
                                setDescribe(true);
                            } else {
                                window.location.href="/portfolio/about";
                            }
                        }}>{startDescribe ? "Neat! Tell me more" : "The 3 'P's that describe me"}</button>
                        <img alt="arrow" src={arrow} style={{height: "108px", width: "81px", position: "absolute", zIndex: "25", marginLeft: "133px", bottom: "25rem"}} />
                        <p className="newFont">A computer engineering student</p>
                        {/* <hr className="line"/>
                        <Typing /> */}
                    </Col>
                    
                    {/*Another for illustration*/}
                    <Col md={5}>
                        <img alt="programmer" src={programmer} className="imgHome" />
                    </Col>
                </Row>
            </Container>
        </section>
    )
}
export default Home;