import React from 'react';
import {
  NavBtn
} from './navbarstyle';
import { CgFileDocument, CgGitFork } from "react-icons/cg";
import { AiFillStar, AiOutlineHome, AiOutlineFundProjectionScreen, AiOutlineUser, AiFillGithub, AiFillLinkedin, AiOutlineMail } from "react-icons/ai";
import { NavLink as Link } from 'react-router-dom';
import pdf from './Assets/resum√©2.pdf';


const Navigate = () => {
  return (
    <>
      {/* justify content in nav is sperating credential, div & navbtn */}
      <nav className="navBar">
        <Link className="Credentials" to='/portfolio'>
            KA.
        </Link>
        <div className="navmenu">
          <Link className="navlink1" to='/portfolio'>
            <AiOutlineHome style={{fontSize: "1.8em", padding: "7px"}}/>Home
          </Link>
          <Link className="navlink1" to='/portfolio/about'>
            <AiOutlineUser style={{fontSize: "1.8em", padding: "7px"}} />About
          </Link>
          <Link className="navlink1" to='/portfolio/projects'>
            <AiOutlineFundProjectionScreen style={{fontSize: "1.8em", padding: "7px"}} />Projects
          </Link>
          <Link className="navlink1" to={pdf} target='_blank' rel='noreferrer'>
            <CgFileDocument style={{fontSize: "1.8em", padding: "7px"}} />Resume
          </Link>
          <Link className="navlink1" to='/portfolio/contact'>
            <AiOutlineMail style={{fontSize: "1.8em", padding: "7px"}} />Contact
          </Link>
        </div>
        <NavBtn>
          <Link className="starButton" to={{pathname: "https://github.com/kush458/portfolio"}} target="_blank"> 
          {/* _blank means open the doc in new tab */}
            <CgGitFork style={{fontSize: "1.2em", alignSelf: "center", paddingBottom: "2px"}} />
            <AiFillStar style={{fontSize: "1.2em", alignSelf: "center", paddingBottom: "2px"}} />
          </Link>
          <Link className="starButton" to={{pathname: "https://github.com/kush458"}} target="_blank"> 
          {/* _blank means open the doc in new tab */}
            <AiFillGithub style={{fontSize: "1.3em", alignSelf: "center", paddingBottom: "2px"}} />
          </Link>
          <Link className="starButton" to={{pathname: "https://www.linkedin.com/in/kushar77/"}} target="_blank"> 
          {/* _blank means open the doc in new tab */}
            <AiFillLinkedin style={{fontSize: "1.3em", alignSelf: "center", paddingBottom: "2px"}} />
          </Link>
        </NavBtn>
      </nav>
    </>
  );
};

export default Navigate;